import React, {useState, useEffect} from 'react'

const Player = (props) => {
    const [score, setScore] = useState(props.player.score)

    const addScore = () => {
        const data = {
            name: props.player.name,
            score: 1
        }
        setScore(score + 1)
        props.updateScore(data)
    }

    const reduceScore = () => {
        const data = {
            name: props.player.name,
            score: -1
        }
        setScore(score - 1)
        props.updateScore(data)
    }

    return (
        <div className="singlePlayer">
            {props.player.name} {score}
                <button onClick={reduceScore}>-</button>
                <button onClick={addScore}>+</button>
        </div>
    )
}

export default Player