import React, { useState } from 'react'
import Player from './Player'
import './App.css'

const App = () => {

  const [init, setInit] = useState(false)
  const [allAdded, setAllAdded] = useState(false)
  const [players, setPlayers] = useState([])
  const [newPlayer, setNewPlayer] = useState('')
  const [winner, setWinner] = useState(false)
  const [winnerName, setWinnerName] = useState('')
  const [over50, setOver50] = useState(false)

  const ohNohMessage = `Voi ei! Yli meni!`

  const start = () => {
    setInit(true)
  }

  const ready = () => {
    setAllAdded(true)
  }

  const addPlayer = (event) => {
    event.preventDefault()
    const newPlayerList = players

    const newName = {
      name: newPlayer,
      score: 0
    }

    newPlayerList.push(newName)
    setPlayers(newPlayerList)
    setNewPlayer('')
  }

  const updateScore = (props) => {
    const playerList = players

    const score = playerList.filter( player => player.name === props.name)

    score[0].score += props.score

    if (score[0].score === 50) {
      setWinner(true)
      setWinnerName(score[0].name)
    }

    if (score[0].score > 50) {
      setOver50(true)
      setTimeout( () => {
        setOver50(false)
      }, 2000)
    }

    playerList.map ((player) => {
      if (player.name === score[0].name) {
        return score[0]
      } else {
        return player
      }
    }  
    )

    setPlayers(playerList)
  }

  const handleChange = (event) => {
    setNewPlayer(event.target.value)
  }
 
  if (init === false) {
    return (
      <div>
        <h1>Mölkky tracker</h1>
        <div className="score">Keep the score</div>
        <div id="aloitusruutu"><button onClick={start} id="start">Aloita</button></div>
      </div>
    )
  } else if (allAdded === false) {
    return (
      <div className="flex">
        <h3>Pelaajat</h3>
        <form id="players" onSubmit={addPlayer}>
          <input type="text" value={newPlayer} onChange={handleChange}></input>
          <div><button id="pelaaja" type="submit">Lisää pelaaja</button></div>
        </form>
        <button id="second" onClick={ready}>Valmis!</button>
        {players.map((player) => 
          <div className="players" key={player.name}>{player.name}</div>)}
      </div>
    )
  } else if (winner === true) {
    return (
      <div>
      <h1>
        Meillä on voittaja! 
      </h1>
      <div id="winner">{winnerName}</div>
      </div>
    )
  }

  return (
    <div>
      {players.map( (player) => 
        <Player key={player.name}
          player={player} 
          updateScore={updateScore} />)}
    </div>  
  ) 
  
}

export default App;
